.sidebar {
  color: #eee;

  &__head {
    font-weight: 300;
    border-bottom: 1px solid rgb(126, 121, 121);
    display: flex;
    align-items: center;
    gap: 1rem;
    height: 7rem;
    padding: 0 1rem;
    justify-content: center;
    @media only screen and (max-width: 950px) {
      display: none;
    }
  }

  &__container {
    // padding: 1rem;
    margin-top: 4rem;
  }

  &__item {
    display: flex;
    align-items: center;
    gap: 1rem;
    color: #eee;
    padding: 1rem;
    font-size: 1.8rem;
    transition: 0.2s;
    &:hover {
      background-color: #3f51b5;
    }

    svg {
      fill: currentColor;
    }
  }
}

.active {
  background-color: #3f51b5;
}

.item {
  display: block;
  @media only screen and (max-width: 950px) {
    display: none;
  }
}

.icon {
  @media only screen and (max-width: 950px) {
    width: 3rem;
    height: 3rem;
  }
}
